.footer {
  background-color: #edeff1;
  overflow: hidden;
  &__head {
    @include breakpoint(max-width 1200px) {
      display: none;
    }
    text-align: center;
    border-bottom: 3px solid #edeff1;
    h1 {
      font-size: 72px;
      font-family: 'Montserrat';
      font-weight: 500;
      position: absolute;
      top: 18px;
      left: 270px;
    }
    h2 {
      font-size: 50px;
      font-family: 'Montserrat';
      font-weight: 300;
      line-height: 105px;
      color: #94d5cf;
      margin: 0;
    }
  }
  p {
    font-family:'Montserrat';
    font-size: 18px;
    font-weight: 300;
  }
  span>p{
    font-weight: 700;
    margin: 0;
  }
  &__contact {
    @include breakpoint(max-width 991px) {
      margin: 0 auto;
      margin-top: 30px;
      width: 300px;
      padding: 0;
    }
    padding-left: 50px;
    margin-top: 50px;
    img {
      float: left;
    }
    p {
      font-size: 17px;
      font-family: 'Montserrat';
      font-weight: 300;
      position: relative;
      clear: both;
      left: 30px;
      top: -25px;
    }
  }
}